        about: ["I began learning to program after a motorcycle accident which left me couch-ridden for some time. Without a doubt, my helmet saved my brain and my life. Wild Men Die is a concept PSA dedicated to that miraculous piece of simple, and efficeint technology. I used this build to channel a difficult experience into a project with a singular, positive purpose: get motorcycle riders to protect their lives.", "There’s no revolutionary front end javascript framework involved here. Just ScrollMagic for the pinning, and soft parallax effects, and lots of hard work, css transitions and jQuery.", "All copy was discussed in depth by me and my project partner. I researched to find our target demographic (young, uneducated males), and crafted copy (short, terse prose, catchy titles, and language that didn’t feel preachy or judgmental) to try to accomodate our user.", "My favorite feature is the video scrubber “hook”. It was concepted by my partner, and designed to immediately engage a user with a whimsical and interactive animation in order to counterbalance the serious nature of the subject matter. The placeholder video is a prototype, intended to be replaced by two falling balloons - one helmeted which survives, and one naked which bursts. It was created by cutting a video shot with a high speed camera  into over 100 jpegs. To do this, I learned how to use ffmpeg, a powerful command line video editing tool, and paired the image element to a jQuery based scrubbing plugin."]
