        about: ["EyeMed is the vision care plan division of Luxottica Group, the largest eyewear brand in the world. As an entity, they possess several websites to service their various different demographics: individual members, businesses looking to purchase eye insurance packages, brokers, and providers.", "My agency of employ secured a two-tiered contract with EyeMed, with the ultimate goal of bringing all their disparate web presences under a single unified experience. This is the pilot site for that vision, and my primary responsibilities were the creation of reusable front-end templates, the scaffolding of those templates with Jekyll, and the merger of that code with Cloudcannon to provide user CMS access.", "Major concerns for this project included a minimum of level 2 ADA compliance, a metric by which websites are judged on their accessibility for people of varying disabilities.", "Another challenge was the creation a complex, two-tiered nav, allowing for quick navigation from within the current demographic site section, as well as overall navigation throughout the global site. The solve for this was to implement a non-javascript dependant accordion in mobile, and a flexbox based dropdown for desktop users.", "As an agency, we have been moving aggressively toward SVG-only iconography, and this site signified the final sea-change to that format. Off-home pages give CloudCannon users access to choose one of four primary color templates to be cascaded down the page. Those colors can be supplied to the SVG code blocks via css to allow the icons to inherit their page’s selected color theme."]
