        about: ["My first agency position offered me the opportunity to develop a pro-bono website for the Boys and Girls Clubs of Greater Houston. The client’s primary ask was that we help them to grow their online donations.", "I researched donation behavior and discovered a vital problem that needed to be addressed in our UX:", "People who decide to make a donation will receive a serotonin uptick regardless of whether they are successfully able to finish completing their donation. The unconscious mind realizes that it genuinely wanted to do something altruistic, and gives itself a big pat on the back. Once this happens, a user is happy to ditch the donation process if it becomes too difficult, or too tedious to complete because their brain has already told them they made the donation.", "My proposed and adopted solution to this problem was the integration of the global donation CTA into a fixed nav so that a user on the site is only ever one click away from beginning their donation workflow."]
