        about: ["Makino invented the commercially viable horizontal machining center and their R&D department is recognized as the world leader in metal-cutting machine design and technology development. For this agency project I was tasked to build their new website’s custom front end with Pattern Lab. What you see here is a kitchen sink that is being prepared for handoff to my co-worker and backend developer.", "This site boasts the most complex nav I have put into production, complete with dropdown, click throughs, and onscroll transition states. I leveraged Pattern Lab’s webpack build chain in order to write modularized javascript components with modern ES6 syntax. These components are easily imported into a clean and concise main.js entry point.", "Webpack also allowed me to easily create a custom Vue component for the webinar filter/reveal section with vue-loader and Vue template syntax."]
