        image: this.images ? this.images.map : this.gottenImages.map,
        modifier: "align-top",
        about: ["Norton sells a diverse selection of billboards throughout the greater Cincinnati, Ohio area. I was responsible for creating their online buyer's tool built with the Google Maps API. This application allows prospective buyers to browse Norton’s inventory by billboard type and location. Buyers can also save their selection, leave the page, and come back to it later. Once they have a list of interests finalized, they can finally email it to themselves from the application.", "Norton was able to furnish us with a list of their products in JSON format. I was given unique identifiers for each billboard object, a billboard type to pair a marker color to, and a lat-long to place the markers. Mustache templating was leveraged to create each info window.", "JQuery’s $.grep() function provided an easy and cross-browser compatible way to clone selected billboards into the user’s saved list array. Objects are referential data types in JavaScript, so click events on each of these saved items could be used to trigger actions their corresponding google map marker. Browser local storage was leveraged for saving lists, and as icing on the cake, query parameters could be passed from CTA buttons on the home page to trigger filter actions to the markers on initial page load."]
