<template>
  <div class="page page-work" @keydown.esc="closeInfo">
    <div class="pagewrap ">
      <div class="work">
        <app-work-item v-for="(site, index) in websites" :site="site" :key="site+index" @cacheVideo="cacheVideo($event, index)" @openInfo="openInfo"></app-work-item>
        <transition :name="transition" mode="out-in">
          <div id="aboutWork" ref="aboutWork" class="about-work" v-if="workItemOpened">
            <app-close @close="closeInfo"></app-close>
            <div class="content-wrap">
              <div class="media">
                <div class="video-wrap">
                  <video v-if="!isMobileSafari" ref="video" :src="vidBase+currentWorkItem.video+'.mp4'" autoplay muted loop></video>
                  <video v-else ref="video" :src="vidBase+currentWorkItem.video+'.mp4'" :poster="currentWorkItem.image" muted loop controls></video>
                </div>
                <a :class="{strikethrough:!hasUrl}" class="to-site-button" target="_blank" :href="currentWorkItem.url">view site</a>
                <a v-if="currentIndex > 0" href="/" class="back-button" :class="{strikethrough: !(currentIndex > 0)}" @click.prevent="changeCurrentWebsite(currentIndex - 1)">prev</a>
                <a v-else class="back-button" :class="{strikethrough: !(currentIndex > 0)}" @click.prevent="changeCurrentWebsite(currentIndex - 1)">prev</a>
                <a v-if="currentIndex < websites.length -1" href="/" class="next-button" :class="{strikethrough: !(currentIndex < websites.length -1)}" @click.prevent="changeCurrentWebsite(currentIndex + 1)">next</a>
                <a v-else class="next-button" :class="{strikethrough: !(currentIndex < websites.length -1)}" @click.prevent="changeCurrentWebsite(currentIndex + 1)">next</a>
              </div>
              <div class="content">
                <h3>{{currentWorkItem.title}}</h3>
                <p v-for="(paragraph, index) in currentWorkItem.about" :key="index">{{paragraph}}</p>
              </div>
            </div>
          </div>
        </transition>
      </div>
    </div>
  </div>
</template>
<script>
import Work from "../components/Work.vue";
import Close from "../components/svg/Close.vue";
import { eventBus } from "../main";
import { isMobileSafari } from "../main";
const increment = 0.075;
const scrollSettings = {
  alignToTop: true,
  behavior: "instant",
  block: "start",
  inline: "start"
};
export default {
  components: {
    appWorkItem: Work,
    appClose: Close
  },
  data() {
    return {
      strikethrough: "strikethrough",
      gottenImages: null,
      currentWorkItem: null,
      nextWorkItem: null,
      currentIndex: 0,
      cachedIndex: null,
      workItemOpened: false,
      vidBase: "http://d12dfyjdyr4tom.cloudfront.net/",
      isMobileSafari: isMobileSafari
    };
  },
  methods: {
    playVideo() {
      if (!isMobileSafari) {
        this.$refs.video.play();
      }
    },
    cacheVideo(currentWorkItem, index) {
      this.currentWorkItem = currentWorkItem;
      this.currentIndex = index;
      let video = document.createElement("video");
      let source = this.vidBase + currentWorkItem.video + ".mp4";
      video.src = source;
    },
    cacheVideoModalOpen(index) {
      let itemToCache = this.websites[index];
      this.nextWorkItem = itemToCache;
      this.cachedIndex = index;
      let video = document.createElement("video");
      let source = this.vidBase + itemToCache.video + ".mp4";
      video.src = source;
    },
    changeCurrentWebsite(index) {
      this.currentWorkItem = this.websites[index];
      this.currentIndex = index;
      if (index < this.websites.length - 1) {
        this.cacheVideoModalOpen(index + 1);
      }
      if (index > 0) {
        this.cacheVideoModalOpen(index - 1);
      }
      const contentBox = this.$el.querySelector(".content-wrap");
      if (window.innerWidth > window.innerHeight) {
        contentBox.scrollIntoView(scrollSettings);
      }
      this.$nextTick(() => {
        this.playVideo();
      });
    },
    openInfo() {
      this.workItemOpened = true;
      eventBus.$emit("aboutWindowOpened", true);
      this.$nextTick(() => {
        this.playVideo();
      });
      if (
        this.currentIndex < this.websites.length - 1 ||
        this.currentIndex == 0
      ) {
        this.cacheVideoModalOpen(this.currentIndex + 1);
      }
      if (this.currentIndex > 0) {
        this.cacheVideoModalOpen(this.currentIndex - 1);
      }
    },
    closeInfo() {
      this.workItemOpened = false;
      eventBus.$emit("aboutWindowClosed", false);
    }
  },
  props: ["images", "transition"],
  computed: {
    hasUrl() {
      return this.currentWorkItem.url;
    },
    websites() {
      return [
        {
          title: "Wild Men Die",
          url: "http://wildmendie.com/",
          video: "wildmendie",
          image: this.images ? this.images.WMD : this.gottenImages.WMD,
          about: [
            "I began learning to program after a motorcycle accident which left me couch-ridden for some time. Without a doubt, my helmet saved my brain and my life. Wild Men Die is a concept PSA dedicated to that miraculous piece of simple, and efficeint technology. I used this build to channel a difficult experience into a project with a singular, positive purpose: get motorcycle riders to protect their lives.",
            "There’s no revolutionary front end javascript framework involved here. Just ScrollMagic for the pinning, and soft parallax effects, and lots of hard work, css transitions and jQuery.",
            "All copy was discussed in depth by me and my project partner. I researched to find our target demographic (young, uneducated males), and crafted copy (short, terse prose, catchy titles, and language that didn’t feel preachy or judgmental) to try to accomodate our user.",
            "My favorite feature is the video scrubber “hook”. It was concepted by my partner, and designed to immediately engage a user with a whimsical and interactive animation in order to counterbalance the serious nature of the subject matter. The placeholder video is a prototype, intended to be replaced by two falling balloons - one helmeted which survives, and one naked which bursts. It was created by cutting a video shot with a high speed camera  into over 100 jpegs. To do this, I learned how to use ffmpeg, a powerful command line video editing tool, and paired the image element to a jQuery based scrubbing plugin."
          ]
        },
        {
          title: "EyeMed",
          url: "http://vocal-smokescreen.cloudvent.net/employers/overview/",
          video: "eyemed_alt",
          image: this.images ? this.images.eyemed : this.gottenImages.eyemed,
          modifier: "align-top",
          about: [
            "EyeMed is the vision care plan division of Luxottica Group, the largest eyewear brand in the world. As an entity, they possess several websites to service their various different demographics: individual members, businesses looking to purchase eye insurance packages, brokers, and providers.",
            "My agency of employ secured a two-tiered contract with EyeMed, with the ultimate goal of bringing all their disparate web presences under a single unified experience. This is the pilot site for that vision, and my primary responsibilities were the creation of reusable front-end templates, the scaffolding of those templates with Jekyll, and the merger of that code with Cloudcannon to provide user CMS access.",
            "Major concerns for this project included a minimum of level 2 ADA compliance, a metric by which websites are judged on their accessibility for people of varying disabilities.",
            "Another challenge was the creation a complex, two-tiered nav, allowing for quick navigation from within the current demographic site section, as well as overall navigation throughout the global site. The solve for this was to implement a non-javascript dependant accordion in mobile, and a flexbox based dropdown for desktop users.",
            "As an agency, we have been moving aggressively toward SVG-only iconography, and this site signified the final sea-change to that format. Off-home pages give CloudCannon users access to choose one of four primary color templates to be cascaded down the page. Those colors can be supplied to the SVG code blocks via css to allow the icons to inherit their page’s selected color theme."
          ]
        },
        {
          title: "Boys and Girls Clubs",
          url: "http://bgcgh.org/",
          video: "bgcgh",
          image: this.images ? this.images.BGC : this.gottenImages.BGC,
          modifier: "align-top",
          about: [
            "My first agency position offered me the opportunity to develop a pro-bono website for the Boys and Girls Clubs of Greater Houston. The client’s primary ask was that we help them to grow their online donations.",
            "I researched donation behavior and discovered a vital problem that needed to be addressed in our UX:",
            "People who decide to make a donation will receive a serotonin uptick regardless of whether they are successfully able to finish completing their donation. The unconscious mind realizes that it genuinely wanted to do something altruistic, and gives itself a big pat on the back. Once this happens, a user is happy to ditch the donation process if it becomes too difficult, or too tedious to complete because their brain has already told them they made the donation.",
            "My proposed and adopted solution to this problem was the integration of the global donation CTA into a fixed nav so that a user on the site is only ever one click away from beginning their donation workflow."
          ]
        },
        {
          title: "Makino",
          url:
            "https://thinkoodle.github.io/makino-patternlab-public/patterns/04-layouts-sink-sink/04-layouts-sink-sink.rendered.html",
          video: "makino_alt",
          image: this.images ? this.images.makino : this.gottenImages.makino,
          modifier: "align-top",
          about: [
            "Makino invented the commercially viable horizontal machining center and their R&D department is recognized as the world leader in metal-cutting machine design and technology development. For this agency project I was tasked to build their new website’s custom front end with Pattern Lab. What you see here is a kitchen sink that is being prepared for handoff to my co-worker and backend developer.",
            "This site boasts the most complex nav I have put into production, complete with dropdown, click throughs, and onscroll transition states. I leveraged Pattern Lab’s webpack build chain in order to write modularized javascript components with modern ES6 syntax. These components are easily imported into a clean and concise main.js entry point.",
            "Webpack also allowed me to easily create a custom Vue component for the webinar filter/reveal section with vue-loader and Vue template syntax."
          ]
        },
        {
          title: "Patrick Cudahy",
          url: "http://rugged-bat.cloudvent.net/",
          video: "patrick_cudahy_alt",
          image: this.images
            ? this.images.patrick_cudahy
            : this.gottenImages.patrick_cudahy,
          modifier: "align-top",
          about: [
            "Patrick Cudahy is a legacy American deli and meat brand with historic roots in Wisconsin, and I am currently building their new website. This project features a familiar technology stack: Patternlab for the creation of molecules and layouts, Jekyll for static site generation from accessible and iterable data points, and CloudCannon to provide content entry CMS access.",
            "Before beginning this dev cycle, I took the opportunity to re-engineer our Patternlab-Jekyll build chain, and allowed these exclusive platforms, via webpack, to build from the same entry points. I generated a boilerplate repo from this setup and pushed it to Github to help usher in the ES6 era at my current agency."
          ]
        },
        {
          title: "Mercy Health",
          url: "https://blog.mercy.com/",
          video: "mercy",
          image: this.images ? this.images.mercy : this.gottenImages.mercy,
          modifier: "align-top",
          about: [
            "Mercy is a non-profit healthcare provider that services millions of patients in the Midwestern US. My agency of employ contracted their new blog, and I was responsible for building this custom theme in Pattern Lab for handoff to a WordPress dev. I have also coded some PHP used in conjunction with the Advanced Custom Fields plugin.",
            "Carousels typically don’t convert, they confuse users, and clients always seem to request them. My designer and I decided to take on the challenge of creating one which users actually interact with. To this end, a little jQuery, and some DOM traversal helped me create the custom carousel icon buttons and border styles.",
            "The wave shape present on the bottom of the carousel echoes established Mercy branding and serves to guide the user’s eye to the carousel’s interactive elements. To achieve the effect, I placed a pseudo-element with an SVG background image at the bottom of the carousel with absolute positioning."
          ]
        },
        {
          title: "Norton Advertising",
          url: "http://www.norton-outdoor.com/browse-inventory",
          video: "norton",
          image: this.images ? this.images.map : this.gottenImages.map,
          modifier: "align-top",
          about: [
            "Norton sells a diverse selection of billboards throughout the greater Cincinnati, Ohio area. I was responsible for creating their online buyer's tool built with the Google Maps API. This application allows prospective buyers to browse Norton’s inventory by billboard type and location. Buyers can also save their selection, leave the page, and come back to it later. Once they have a list of interests finalized, they can finally email it to themselves from the application.",
            "Norton was able to furnish us with a list of their products in JSON format. I was given unique identifiers for each billboard object, a billboard type to pair a marker color to, and a lat-long to place the markers. Mustache templating was leveraged to create each info window.",
            "JQuery’s $.grep() function provided an easy and cross-browser compatible way to clone selected billboards into the user’s saved list array. Objects are referential data types in JavaScript, so click events on each of these saved items could be used to trigger actions their corresponding google map marker. Browser local storage was leveraged for saving lists, and as icing on the cake, query parameters could be passed from CTA buttons on the home page to trigger filter actions to the markers on initial page load."
          ]
        },
        {
          title: "Carter Knife Co.",
          url: "http://www.carterknifeco.com/#/",
          video: "carterknifeco",
          image: this.images
            ? this.images.carterknifeco
            : this.gottenImages.carterknifeco,
          about: [
            "Around six years ago, my little brother Matthew packed it all up and moved across the country to live out his dreams in the beautiful town of Bend, Oregon. At some point during his journey, the idea came to him to create a home and work space from a used school bus. Carter Knife Company was born.",
            "I had built Matthew a WordPress site for his business a couple years ago, complete with the WooCommerce plugin, an accompanying store section, and the ability for him to update and edit his inventory and sell knives online via PayPal. After a while however, Matthew found himself unwilling to deal with the intricacies of that platform, and not updating his own content at all. It was time for a new site, with a simpler, stripped down way for him to get his inventory online, and for his buyers to place their orders.",
            "Matt’s main ask was to have an interactive section where buyers could piece a knife together. I thought Vue.js’ two way data binding would be a great tool for such a feature, and opted to scaffold the new site with vue-cli. The build-a-knife page was a great success. Immediately following its production, Matt noticed customers were communicating with a new level of confidence and a vocabulary which he had not previously experienced.",
            // Other features of note include a scrolling lifestyle and product feed, built with the Instagram API. The feed allows Matthew to update his products using a simple, and familiar platform.
            "Finally, for some fun on his landing page, I sliced a new logo Matthew had purchased into four layers, and created the three dimensional hover effect with vanilla-tilt.js."
          ]
        },
        {
          title: "Margherita Meats",
          url: "http://www.margheritameats.com/",
          video: "margherita",
          image: this.images
            ? this.images.margherita
            : this.gottenImages.margherita,
          about: [
            "Margherita Meats is another agency build. This was my first complete production site with Jekyll, a blogging and static site generator that leverages Markdown templates, JSON, CSV, or YAML data types to quickly iterate and generate layouts.",
            "This was a rebuild of an existing website, and dependant on the original's data. In order to convert that data into an appropriate Jekyll format, I wrote a gulp script that converted a provided JSON file into Markdown files for each product and recipe page.",
            "Margherita’s simple home page, with a short nav, and two large, intuitive image links helped inspired the design for this site's stripped down navigation."
          ]
        },
        {
          title: "Discogs Dash",
          url: "http://discogdash.com/#/search",
          video: "discogs",
          image: this.images ? this.images.discogs : this.gottenImages.discogs,
          modifier: "align-top",
          about: [
            "Discogs is a crowd-sourced, online music database and marketplace. This project is a concept dashboard and purchasing tool for music stores that would utilizes Discogs to compare current prices on selected releases.",
            "Discogs Dash is primarily a way for me to explore and learn about Vuex. Scaffolded in vue-cli, it allows me to make calls to the Discogs API based on the user’s search parameters and keep stash is received in a central data store. Once gathered, that data can be passed directly to child components, to be rendered and interacted with, rather than through a series of parent-child props and emission listeners.",
            "This project also leverages API calls to Google’s Firebase in order to save listings, and attach logged users to their personal libraries."
          ]
        },
        {
          title: "Hyperdrive",
          url: "http://hyperdrivei.com/",
          video: "hyperdrive",
          image: this.images
            ? this.images.hyperdrive
            : this.gottenImages.hyperdrive,
          modifier: "align-top",
          about: [
            "Hyperdrive is a digital marketing firm in Cincinnati, Ohio which gave me my first agency position. They were in desperate need of a website overhaul. Their design team had already drafted the comps necessary, but their scheduled client workload was preventing them from taking care of their own brand needs.",
            "Insert the new guy. Unattached to any client work yet, I was allowed take the reins and build the site from the ground up. Hyperdrive is a Visual Studio shop, and this project taught me the basics of the MVC development pattern. "
          ]
        },
        // {
        //   title: "Personal Resumes",
        //   url: "http://ryancarterresume.com/",
        //   video: "resumes",
        //   image: this.images ? this.images.resume : this.gottenImages.resume,
        //   modifier: "align-top",
        //   about: [
        //     "Resume websites for eager job seekers, leveraging the power of Json Resume."
        //   ]
        // },
        {
          title: "Plaid Room Records",
          url: "http://howtheturntables.com/",
          video: "plaidroom_alt",
          image: this.images ? this.images.plaid : this.gottenImages.plaid,
          about: [
            "Plaid Room Records is a music store located in Loveland, Ohio, owned and ran by a pair of brothers I am very good friends with. Today, they have a Shopify store, capable of tracking pre-releases and generating sales via user-notification sign ups. Very hoity-toity, but it wasn’t always so!",
            "When the shop initially opened, their first website was built and maintained using Google Sites. It was a simple site that allowed easy access for daily updating of content, but lacked some fundamental capabilities of more modern web development, mainly it had no mobile breakpoints. Users who wanted to use the site on their phones were relegated to the old pinch and zoom method of surfing desktop layouts in mobile browsers.",
            "I built this concept site with Plaid Room’s permission to bolster my portfolio at the time, and to show my friends some modern web techniques they were missing out on.",
            "Easily my favorite feature is the simulated spinning record effect on landing. I wanted to create a space where a record store or label could feature an artist or song of the day, and loved the idea of pairing their branding to this central “hook”. The effect was achieved by the toggling of a simple, infinite css rotation animation, a little jQuery, and some math to help me figure out how long each rotation should be to have the “record” land back at twelve o’clock when the song ends."
          ]
        },
        {
          title: "Bake!",
          url: "http://wakeandbakesale.com",
          video: "bake",
          image: this.images ? this.images.bake : this.gottenImages.bake,
          about: [
            "Bake started as a passion project and was something I was admittedly a little tentative about actually creating for fear of alienating more conservative audiences. Despite these reservations, I feel that marijuana represents a strong and exciting emerging market. As it transitions into its legality, the plant poses serious opportunity for a new industry. Platforms built around its commerce will inevitably need marketing and branding strategies. The opportunity to create a unique development concept was too great, and so here it is.",
            "Like it or not, marijuana is already here: currently legal in 4 states, and available for medicinal use in 24 others, as well as in the nation’s capital. With 15 more states considering decriminalization and 10 more states contesting for recreational legalization of marijuana, smart entrepreneurs are seeing it for the business opportunity it can and is already becoming. The big question for those who choose to stake their dollar in this burgeoning industry becomes how to market it most effectively?",
            "Pew research shows that although public support for marijuana is rapidly outpacing opposition, a simple stigma still remains: it smells. People may believe in it, and desire its legality, but a majority of them prefer not having to deal with it secondhand in their day to day. The key to effectively marketing marijuana will be discretion. Its strength as a consumable commodity, vs. simmilar products like tabacco, lies in its ability to be ingested rather than smoked.",
            "BAKE! was conceived of as a way to market marijuana strategically, with the aim of providing a unique experience that helps to curb the issue of flagrant use, while broadening its appeal to new markets. A designer marijuana bakery and café, run by professional chefs who know how to pair flavors appropriately, would attract young foodies, hip millennials, and baby-boomer parents seeking a shared adventure with their grown children. This site represents my take on how to represent that bakery's desired branding identity in the digital space."
          ]
        },
        {
          title: "Creemboards",
          url: "http://creemboards.com/",
          video: "creemboards",
          image: this.images ? this.images.cream : this.gottenImages.cream,
          about: [
            "This branding concept for a skateboard company features a “dream team” of some of the most prolific professional skateboarders to date, with their bios paired to a background video of some of their best skating.",
            "Background videos are becoming common to the point of cliché, and I thought I’d explore what I consider to be more appropriate use case for such a feature. Skate videos have long been the primary format for skateboarding companies to market their pros and products. As web becomes a more dominant medium, I believe the interesting challenge becomes how best to integrate these two formats symbiotically."
          ]
        }
      ];
    }
  },
  created() {
    if (!this.images) {
      let images = {
        cream: require("../assets/images/cream.jpg"),
        bake: require("../assets/images/bake.jpg"),
        plaid: require("../assets/images/plaid.jpg"),
        hyperdrive: require("../assets/images/hyperdrive.jpg"),
        discogs: require("../assets/images/discogs.jpg"),
        margherita: require("../assets/images/margherita.jpg"),
        carterknifeco: require("../assets/images/carterknifeco.jpg"),
        patrick_cudahy: require("../assets/images/patrick_cudahy.jpg"),
        map: require("../assets/images/map.jpg"),
        mercy: require("../assets/images/mercy.jpg"),
        makino: require("../assets/images/makino.jpg"),
        BGC: require("../assets/images/BGC.jpg"),
        eyemed: require("../assets/images/eyemed.jpg"),
        WMD: require("../assets/images/wmd.jpg")
      };
      this.gottenImages = images;
    }
    let animationDelay = 0;
    for (let site of this.websites) {
      site.animationDelay = animationDelay;
      animationDelay += increment;
    }
  }
};
</script>


